import store from "@/store";
import axios from "axios";
import {  Message } from "element-ui";


const service = axios.create({
    baseURL: process.env.VUE_APP_HTTP_BASE_URL,
    timeout: 50000,
});

//所有请求设置了授权头信息【请求头中设置cookie信息，一直携带，判权】
// axios.defaults.headers.common['Authorization'] = localStorage.getItem('tyytrade-web_TOKEN');
//这里是设置发送json格式参数
// axios.defaults.headers.post['Content-Type'] = 'application/json';

//请求拦截
service.interceptors.request.use(
    (config) => {
        //请求前处理
        // if(store.getters.token) {
        //     //这里的getToken方法需要引入auth.js
        //     config.headers['token'] = getToken()
        // }
        return config;
    },
    (error) => {
        console.log(error);
        return Promise.reject(error);
    }
)
//响应拦截
service.interceptors.response.use(
    //请求成功
    // res => res.status === 200 ? Promise.resolve(res) : Promise.reject(res),
    (res) => {
        if(res.status === 200) {
            return Promise.resolve(res)
        } else {
            return Promise.reject(res)
        }
    },
    //请求失败
    (error) => {
        //可根据不同的状态去区分不同的错误，达到不同效果
        if(error.response.status){
            if(error.response.status === 404) {
                Message({
                    message:'404请求不存在！',
                    type: "error",
                    duration: 2 * 1000,
                })
            } else {
                Message({
                    message:`其他错误:${response.status}`,
                    type: "error",
                    duration: 2 * 1000,
                })
            }
        }
        return Promise.reject(error);
    }
);

export default service;

