<template>
    <div style="margin-top:1rem;color:black">
    <el-page-header @back="goBack" content="成果预览" style="padding-left:10px"></el-page-header>
    <div style="width:100%;display:flex;justify-content: center;align-items: center;">
     <div style="width:80%;padding: 20px;margin-top:0.5rem">
        <div style="width:100%;color:black;border:1px solid gray;border-radius: 2px;">
            <el-row>
                <el-col :span="16">
                    <div style="padding:20px;width:100%;height:100%;">
                        <el-form ref="formup"   label-width="80px">
                            <el-row :gutter="20">
                                <el-col :span="12">
                                    <el-form-item label="成果名称:">
                                        {{ previewMsg.resultName }}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="成果级别:" >
                                        {{ previewMsg.resultDetails.level }}
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row :gutter="20">
                                <el-col :span="12">
                                    <el-form-item label="成果类型:">
                                        {{ previewMsg.resultDetails.type }}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="数据格式:" >
                                        {{ previewMsg.contextType }}
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-form-item label="所属项目:" v-show="previewMsg.resultDetails.level === '项目级' || previewMsg.resultDetails.level === '工点级'">
                                {{ previewMsg.resultDetails.demoFormName }}
                            </el-form-item>
                            <el-form-item label="所属工点:" v-show="previewMsg.resultDetails.level === '工点级'">
                                {{ previewMsg.resultDetails.dotFormName }}
                            </el-form-item>
                            <!-- label-width="100px" -->
                            <el-form-item label="勘察阶断:" v-show="previewMsg.resultDetails.level === '项目级' || previewMsg.resultDetails.level === '工点级'">
                                {{ previewMsg.resultDetails.exploreStage }}
                            </el-form-item>
                            <el-form-item label="成果描述:">
                                {{ previewMsg.resultRemark }}
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div style="width:100%;height:430px;padding: 10px;">
                        <!-- <img :src="this.img" style="height:360px;width:100%;border:1px solid gray" alt=""> -->
                        <el-carousel :interval="3000" arrow="always" style="border:2px solid #DCDCDC;">
                            <el-carousel-item v-for="item in showImgs" :key="item.img">
                                <div class="image-container">
                                    <img :src="item" style="max-width: 100%; max-height: 100%;object-fit: contain;" />
                                </div>
                            </el-carousel-item>
                        </el-carousel>
                        <div style="width:100%;display:flex;justify-content: center;padding:15px">
                            <div style="flex:1;text-align: center;">
                                <el-link type="primary" style="font-size:16px" @click="showPreview">预览</el-link>
                            </div>
                            
                        </div>
                    </div>
                </el-col>
            </el-row>
            <!-- <el-dialog :modal-append-to-body=false  :visible.sync="previewVisible" width="50%">
                <el-image :src="this.img" style="width: 100%" fit="contain"></el-image>
            </el-dialog> -->
            <el-dialog :modal-append-to-body=false  :visible.sync="previewVisible">
                <!-- <el-image :src="this.img" style="width: 100%;max-width: 300px;margin:0 auto" fit="contain"></el-image> -->
                <div style="width: 100%;height:450px;margin: auto;line-height: 450px;overflow: hidden;" @wheel="handleWheel">
                    <img :src="currentImage" :style="imageStyle">
                </div>
                <el-button @click="nextImage">下一张</el-button>
            </el-dialog>
        </div>    
        <div style="height:50px;line-height:50px;padding:10px;color: black;">
            <div style="cursor:pointer;height:50px;display:inline-block;line-height:50px"><i class="el-icon-star-off" style="font-size:30px;color:red"></i>&nbsp&nbsp&nbsp加入关注</div>
            <div style="cursor:pointer;height:50px;display:inline-block;line-height:50px;margin-left: 50px;"><i class="el-icon-s-promotion" style="font-size:30px;color:#007EB4"></i>&nbsp&nbsp&nbsp关联数据集（地质商品）</div>
        </div> 
      </div>
    </div></div>
</template>
<script>
import myaxios from '../../utils/httpRequest'
export default {
   data() {
      return {
        zoomLevel: 1,  //预览图缩放
        currentIndex:0,
        showImgs:[], //预览图url组
        img:require('../../assets/images/null.png'),
        previewVisible:false,
        //基本信息
        previewMsg:{
            resultDetails:{},
        },
        //存缩略图文件
        imgFiles:[],
      }
   },
   created(){
   },
   mounted(){
    this.getPreviewInfo();
   },
   computed:{
    currentImage() {
        return this.showImgs[this.currentIndex];
    },
    imageStyle() {
        return {
            transform: `scale(${this.zoomLevel})`,
            transformOrigin: 'center',
            objectFit: 'contain',
            height: '100%',
            width: '100%',
        };
    },
   },
   methods:{
    //控制预览的下一张
    nextImage() {
      this.currentIndex = (this.currentIndex + 1) % this.showImgs.length;
    },
    //滑轮放大缩小预览图片
    handleWheel(event) {
        event.preventDefault();
        const zoomDelta = event.deltaY > 0 ? -0.1 : 0.1;
        this.zoomLevel += zoomDelta;
        // 设置缩放范围
        this.zoomLevel = Math.max(0.1, this.zoomLevel);
    },
    showPreview() {
      this.previewVisible = true;
    },
    goBack() {
        this.$router.back(-1)
    },
    //获取成果预览信息
    async getPreviewInfo() {
        const res = await myaxios.get(`/getGeoResult/${this.$route.params.id}`);
        console.log("全部基本信息",res.data.data);
        this.previewMsg = res.data.data;
        //过滤出缩略图用于展示
        const filterImg = this.previewMsg.annex.filter(item => item.annexType === '3');
        //如果有多张缩略图最后imgFiles是包含多个元素的数组-轮播显示
        for (const element of filterImg) {
            // 直接get得到的是Promise对象，无论是使用 then()方法还是 async/await都可以拿到promise当中的返回结果
            let res = await myaxios.get(`/getAnnexFile/${element.resultId}/${element.fileId}`,{responseType:'blob'});
            console.log(res);
            this.imgFiles.push(res);
        }
        console.log("this.imgFiles",this.imgFiles)
        //将缩略图转化为baes64   
        for(let i = 0; i<this.imgFiles.length; i++){
            const reader = new FileReader();
            //如果你使用普通的函数声明 function(e) {...}，在函数作用域内部，this 的值将不再指向组件实例，而是指向函数本身。这可能会导致 this.img 无法正确访问和更新，因为 this 不再指向组件实例。
            reader.onload = (e) =>{
                const base64Data = e.target.result;
                this.showImgs.push(base64Data);
                // this.showImgs[i] = base64Data
            };
            reader.readAsDataURL(this.imgFiles[i].data);
        };      
        console.log("this.showImgs",this.showImgs)
        //blob转base64
        // const reader = new FileReader();
        // reader.onload = (e) =>{
        //     const base64Data = e.target.result;
        //     // console.log("Base64 数据", base64Data);
        //     this.img = base64Data;
        // };
        // reader.readAsDataURL(this.imgFiles[0].data);
    }
   },
}
</script>
<style lang="scss" scoped>
.image-container {
  height: 100%;
  width: 100%;
  /* border:1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
</style>
